import {
  Links,
  Meta,
  Outlet,
  Scripts,
  ScrollRestoration,
  useLoaderData,
} from '@remix-run/react';
import {Seo} from '@shopify/hydrogen';
import styles from './styles/app.css';
import favicon from '../public/favicon.svg';
import {Layout} from './components/Layout';
import { useState } from 'react';
import Modal from 'react-modal';
import Home from './routes/home';


export const links = () => {
  console.log('styles: ', styles);
  return [
    {rel: 'stylesheet', href: styles},
    {
      rel: 'preconnect',
      href: 'https://cdn.shopify.com',
    },
    {
      rel: 'preconnect',
      href: 'https://shop.app',
    },
    {rel: 'icon', type: 'image/svg+xml', href: favicon},
  ];
};

export async function loader({context}) {

  const layout = await context.storefront.query(LAYOUT_QUERY);
  return {layout};
}

export default function App() {
  const data = useLoaderData();
  const [landingPage, setLandingPage] = useState(true);
  const [miniBrowser, setMiniBrowser] = useState(false);

  function openLandingPage() {
    setLandingPage(true);
  }

  function closeLandingPage() {
    setLandingPage(false);
  }

  function openMiniBrowser() {
    setLandingPage(true);
  }

  function closeMiniBrowser() {
    setLandingPage(false);
  }

  const {name} = data.layout.shop;

  return (
    <html lang="en">
      <head>
        <meta charSet="utf-8" />
        <meta name="viewport" content="width=device-width,initial-scale=1" />
        <Seo />
        <Meta />
        <Links >
          <link href="path/to/your/generated.css" rel="stylesheet"></link>
        </Links>
      </head>
      <body>
      {landingPage && (
         <div>
              <video autoPlay loop muted>
                  <source src='https://i.imgur.com/2jLwS41.mp4' type="video/mp4"/>
              </video>
              <div>
                  <nav>
                    <button onClick={closeLandingPage}>
                        Press to Enter
                    </button>
                  </nav>
              </div>
          </div>
      )}
      {landingPage == false && (
          <div className="Home">
                  <video className="homeVideo" autoPlay loop muted>
                      <source src='https://i.imgur.com/2jLwS41.mp4' type="video/mp4"/>
                  </video>
              <div className="body">
                  <body>
                      
                          <img src="https://i.imgur.com/i9KgFOX.gif"/>
                          <button className="buttonShop" onClick={openMiniBrowser()}>
                              Shop
                          </button>
                      
                      
                      <img src="https://i.imgur.com/xDgYL4R.gif"/>
                          <button className="buttonCart">
                              Cart
                          </button>
                      
                      <img src="https://i.imgur.com/Mm0P4CS.gif"/>
                          <button className="buttonAbout">
                              About
                          </button>
                  </body>
              </div>
              <footer>
                  <div className="footer">
                      <button className="buttonFooter">
                          Start
                      </button>
                  </div>
              </footer>
          </div>
      )}
      {miniBrowser && (
          <Layout>
            <Outlet />
          </Layout>
      )}
        <ScrollRestoration />
        <Scripts />
      </body>
    </html>
  );
}

const LAYOUT_QUERY = `#graphql
  query layout {
    shop {
      name
      description
    }
  }
`;
